.slider-controls {
	display: flex;
	z-index: 10; }

.slider-arrow {
	width: 45px;
	height: 45px;
	background-color: #fff;
	margin-right: 5px;
	padding: 10px;
	@include tr;
	@include h {
		cursor: pointer;
		background-color: #eee; }
	svg {
		width: 100%;
		height: 100%;
		fill: $main; }
	&:last-child {
		margin-right: 0; }
	&__prev {
		@include rotate(180); }
	&__next {} }

.slider-counter {
	display: flex;
	align-items: flex-end;
	margin-bottom: 26px;
	&__item {
		color: #fff;
		font-family: EBGaramond;
		font-weight: 700;
		line-height: 14px; }
	&__current {
		font-size: 24px;
		line-height: 14px;
		margin-right: 3px; }
	&__total {
		font-size: 14px;
		line-height: 6px;
		&:before {
			content: "/"; } } }
