@mixin r($width) {
  @media only screen and (max-width: $width+ "px") {
    @content; } }

@mixin h {
  &:hover {
    @content; } }

@mixin vcenter {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%); }

@mixin placeholder-color($color) {
  &::placeholder {
    color: $color; } }

@mixin placeholder {
  &::placeholder {
    @content; } }

@mixin coverdiv {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

@mixin cover {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%; }

@mixin hover {
  .no-touch &:hover {
    @content; } }

@mixin z {
  &:__ {
    @content; } }

@mixin tr {
  transition: all 300ms; }

@mixin trs($ms) {
  transition: all $ms+ms; }

@mixin trc {
  transition: color 300ms; }

@mixin trcs($ms) {
  transition: color $ms+ms; }

@mixin trbgc {
  transition: backround-color 300ms; }

@mixin trbgcs($ms) {
  transition: backround-color $ms+ms; }

@mixin placeholder-color($color) {
  &::placeholder {
    color: $color; } }

@mixin coverdiv {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

@mixin cover {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%; }

@mixin keyframes($name) {
  @-webkit-keyframes #{$name} {
    @content; }
  @-moz-keyframes #{$name} {
    @content; }
  @-ms-keyframes #{$name} {
    @content; }
  @keyframes #{$name} {
    @content; } }

@mixin box {
  @include box-sizing(border-box); }

@mixin scale($var) {
  transform: scale($var); }

@mixin rotate($deg: 0, $m11: 0, $m12: 0, $m21: 0, $m22: 0) {
  transform: rotate($deg + deg);
  filter: progid:DXImageTransform.Microsoft.Matrix(M11=$m11, M12=$m12,M21=$m21, M22=$m22, sizingMethod='auto expand'); }
