.icon-facebook { width: 0.53em; height: 1em; fill: #000; }

.icon-hero-arrow { width: 1em; height: 1em; fill: initial; }

.icon-slider-arrow { width: 0.53em; height: 1em; fill: initial; }

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

html { -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }

input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@media (prefers-reduced-motion: reduce) { * { transition: none !important; } }

/* Slider */
.slick-slider { position: relative; display: block; box-sizing: border-box; -moz-box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; display: none; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide img { display: block; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.header { display: flex; flex-direction: column; overflow: hidden; }

.header__container { width: 100%; padding: 0 15px; max-width: 1150px; margin: 0 auto; }

@media only screen and (max-width: 1024px) { .header__container { padding: 0 30px; } }

.top-header-menu { background-color: #002d5a; }

@media only screen and (max-width: 1024px) { .top-header-menu { display: none; } }

.top-header-menu__container { display: flex; justify-content: flex-end; width: 100%; padding: 0 15px; max-width: 1150px; margin: 0 auto; }

.top-header-menu__item { display: flex; align-items: center; }

.top-header-menu .lang-switcher { margin-left: 70px; }

.bottom-header-menu { display: flex; justify-content: space-between; align-items: center; background-color: #FFF; }

.navigation { display: flex; align-items: center; }

@media only screen and (max-width: 1024px) { .navigation { display: none; } }

.navigation__link { align-content: stretch; line-height: 73px; color: #002d5a; font-family: Roboto; font-size: 14px; font-weight: 500; text-transform: uppercase; margin-right: 50px; transition: all 300ms; }

.navigation__link:last-child { margin-right: 0; }

.navigation__link:hover { text-shadow: 0 0 0.5px #002d5a; }

.navigation-mobile { display: none; position: fixed; top: 0; left: 0; width: 100vw; height: 100vh; justify-content: space-between; flex-direction: column; align-items: center; padding: 35px 30px; background-color: #002d5a; z-index: -10; transform: translateY(-100%); overflow: hidden; transition: all 300ms; }

@media only screen and (max-width: 1024px) { .navigation-mobile { display: flex; } }

.navigation-mobile__is-open { transform: translateY(0); z-index: 999; }

.navigation-mobile__container { display: flex; flex-direction: column; align-items: center; max-width: 270px; }

.navigation-mobile__block { display: flex; }

.navigation-mobile__item { display: flex; flex-wrap: wrap; font-family: Roboto; font-size: 14px; font-weight: 500; text-transform: uppercase; margin-bottom: 32px; transition: all 300ms; }

.navigation-mobile__item:last-child { margin-bottom: 0; }

.navigation-mobile__link { color: #fff; background-color: transparent; }

.navigation-mobile .hamburger { position: absolute; right: 30px; top: 20px; }

.navigation-mobile .contacts { flex-wrap: wrap; justify-content: space-between; }

.navigation-mobile .contacts__item { text-align: center; line-height: 14px; margin-right: 0; min-width: 120px; }

.navigation-mobile .contacts__item:last-child { margin: 0 auto; margin-top: 12px; }

.hero { width: 100%; background: linear-gradient(rgba(0, 64, 128, 0.6), rgba(0, 64, 128, 0.6)), url("/img/hero/bg.jpg") no-repeat center center; background-size: cover; position: relative; }

.hero__container { min-height: 600px; max-width: 1180px; padding: 0 15px; margin: 0 auto; position: relative; }

@media only screen and (max-width: 1024px) { .hero__container { padding: 0 30px; } }

.hero__slider { width: 100%; margin: 0 auto; position: relative; z-index: 2; }

.hero__box { display: flex; flex-direction: column; align-items: flex-end; position: absolute; right: 15px; bottom: 0; z-index: 10; }

@media only screen and (max-width: 1024px) { .hero__box { right: 30px; } }

.hero__item { display: flex; flex-direction: column; justify-content: flex-end; padding: 70px; min-height: 600px; }

@media only screen and (max-width: 1024px) { .hero__item { padding: 48px 0; } }

@media only screen and (max-width: 768px) { .hero__item { justify-content: center; } }

.hero__subtitle { color: #FFF; font-family: "Roboto"; font-size: 12px; font-weight: 300; line-height: 14px; margin-bottom: 25px; }

.hero__title { color: #fff; font-family: Roboto; font-size: 40px; font-weight: 700; line-height: 45px; margin-bottom: 31px; max-width: 524px; }

@media only screen and (max-width: 768px) { .hero__title { font-size: 24px; } }

.hero__counter { color: #fff; font-weight: 700; line-height: 14px; font-size: 24px; }

.slider-controls { display: flex; z-index: 10; }

.slider-arrow { width: 45px; height: 45px; background-color: #fff; margin-right: 5px; padding: 10px; transition: all 300ms; }

.slider-arrow:hover { cursor: pointer; background-color: #eee; }

.slider-arrow svg { width: 100%; height: 100%; fill: #002d5a; }

.slider-arrow:last-child { margin-right: 0; }

.slider-arrow__prev { transform: rotate(180deg); filter: progid:DXImageTransform.Microsoft.Matrix(M11=$m11, M12=$m12,M21=$m21, M22=$m22, sizingMethod='auto expand'); }

.slider-counter { display: flex; align-items: flex-end; margin-bottom: 26px; }

.slider-counter__item { color: #fff; font-family: EBGaramond; font-weight: 700; line-height: 14px; }

.slider-counter__current { font-size: 24px; line-height: 14px; margin-right: 3px; }

.slider-counter__total { font-size: 14px; line-height: 6px; }

.slider-counter__total:before { content: "/"; }

.button { display: flex; width: fit-content; min-width: 170px; justify-content: center; align-items: center; border-radius: 18px; padding: 0 33px; background-color: #fff; color: #002d5a; font-family: "Roboto"; font-size: 15px; font-weight: 500; line-height: 35px; }

.button svg { width: 11px; height: 8px; fill: #002d5a; margin-left: 12px; }

.button__accent { background-color: #002d5a; color: #FFF; }

.contacts { display: flex; align-items: center; }

.contacts__item { font-family: "Roboto"; color: #FFF; font-size: 13px; line-height: 25px; font-weight: 300; margin-right: 18px; background-color: transparent; transition: all 300ms; }

.contacts__item:hover { text-shadow: 0 0 3px #fff; cursor: pointer; }

.contacts__item:last-child { margin-right: 0; }

