.hamburger { width: 38px; min-width: 38px; max-width: 38px; height: 28px; min-height: 28px; max-height: 28px; cursor: pointer; position: relative; display: none; transition: all 300ms; }

@media only screen and (max-width: 1024px) { .hamburger { display: block; } }

.hamburger span { display: block; width: 100%; height: 4px; background-color: #002d5a; border-radius: 3px; position: absolute; transform: rotate(0deg); transition: all 300ms; }

.hamburger span:nth-child(1) { top: 0; }

.hamburger span:nth-child(2) { top: 12px; opacity: 1; right: 0; }

.hamburger span:nth-child(3) { top: 24px; }

.hamburger__is-open span { background-color: #fff; transition: all 300ms; }

.hamburger__is-open span:nth-child(1) { top: 12px; transform: rotate(135deg); }

.hamburger__is-open span:nth-child(2) { opacity: 0; right: -38px; }

.hamburger__is-open span:nth-child(3) { top: 12px; transform: rotate(-135deg); }

.lang-switcher { display: flex; }

.lang-switcher__item { opacity: 0.5; text-transform: uppercase; margin-right: 13px; color: #fff; font-family: Roboto; font-size: 13px; font-weight: 400; }

.lang-switcher__item:last-child { margin-right: 0; }

.lang-switcher__item-active { opacity: 1; }

.logo { width: 103px; height: 51px; }

.logo__image { width: 100%; height: auto; max-height: 51px; }

.text { font-family: "Roboto"; }

.text__300 { font-weight: 300; }

.text__400 { font-weight: 400; }

.text__500 { font-weight: 500; }

.text__600 { font-weight: 600; }

a { text-decoration: none; }

a:hover { text-decoration: none; }
